<template>
    <div>
        <div v-if='load'>
            <div class="card shadow p-4">
                <div class="card-header"><span class="title-card">Data Statistik Covid-19 Di Indonesia</span></div>
                <apexchart height="350" :options="chartOptions" :series="series"></apexchart>
            </div>
        </div>
        <div v-else>
            loading....
        </div>
    </div>
</template>

<script>
import moment from 'moment'
import VueApexCharts from 'vue-apexcharts'
import axios from 'axios'

export default {
    components: {
        apexchart: VueApexCharts,
        },
    data: () => ({
                load: false,
                series: [{
                    name: "Positif",
                    data: []
                    },
                    {
                    name: "Sembuh",
                    data: []
                    }],

            chartOptions: {
                chart:{
                    height: 350,
                    type: 'line',
                    toolbar: {
                        show: false
                    }
                },
                colors: ['red', 'green'],
                dataLabels: {
                    enable: true,
                },
                stroke: {
                    curve: 'straight'
                },
                grid: {
                    borderColor: '#e7e7e7',
                    row: {
                        colors: ['#f3f3f3', 'transparent'],
                        opacity: 0.5
                    },
                },
                markers: {
                    size: 1
                },
                xaxis: {
                    type: 'datetime',
                    categories: [],
                    convertedCatToNumeric: false,
                },
                yaxis: {
                    title: {
                        text: 'Orang',
                },
                min: 0,
                max: 0
                },
                legend: {
                    position: 'top',
                    horizontalAlign: 'right',
                    floating: true,
                    offsetY: 2,
                    offsetX: -10,
                }
            }
        }),
    methods: {
        updatecovid(){
            axios.get('http://localhost:3000/update')
                .then(response => {
                    var temp = 0;
                    for (var i in response.data.harian)
                    {
                        temp + 1;
                        this.series[0].data[i] = response.data.harian[i].jumlah_positif.value;
                        this.series[1].data[i] = response.data.harian[i].jumlah_sembuh.value;
                        //this.series[0].data.push(response.data.harian[i].jumlah_positif.value);
                        if(temp/10 == 0)
                        {
                            this.chartOptions.xaxis.categories.push(this.date_form(response.data.harian[i].key_as_string));
                        }
                    }
                    this.chartOptions.yaxis.max = Math.max.apply(Math, this.series[0].data) + 50;
                    console.log(this.categories);
                }).catch(error => {
                    console.log(error);
                }).finally(() => {this.load = true;});
        },
        date_form: function(value, format){
            if (value) {
                return moment(String(value)).format(format || 'DD/MMMM')
            }
        }
    },
    mounted() {
        this.updatecovid();
    }
}
</script>

<style lang="css" scoped>
    .title-card {
        font-size: 30px;
        font-family: 'Bebas Neue', cursive;
    }
</style>