<script>
import Navbar from '@/components/Navbar.vue'
import ICountUp from 'vue-countup-v2'
import chart from '@/components/Chart'
import axios from 'axios'
import VueAos from 'vue-aos'

export default {
    components: {
        chart,
        ICountUp,
        Navbar,
        VueAos
    },
    data () {
        return {
            unload: true,
            info: null,
            status: null,
            delay: 0,
            options: {
                useEasing: true,
                useGrouping: true,
                separator: ',',
                decimal: '.',
                prefix: '',
                suffix: ''
            }
        }
  },
    mounted() {
        this.updatecovid();
    },
    methods: {
        updatecovid(){
            axios.get('http://localhost:3000/update')
                .then(response => {
                    this.info = response.data;
                    
                }).catch(error => {
                    console.log(error);
                }).finally(() => this.unload = false);

            axios.get('http://localhost:3000/data')
                .then(response => {
                    this.status = response.data;
                    console.log(response.data)
                }).catch(error => {
                    console.log(error);
                }).finally(() => this.unload = false);
        }
    },
    filters: {
        addComma (val) {
            return val.toLocaleString()
        }
    }
}
</script>

